import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import Swal from 'sweetalert2';

import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';

const CustomButton = styled(Button)({
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 16,
    padding: '6px 12px',
    border: '1px solid',
    lineHeight: 1.5,
    backgroundColor: '#FF7170', //   
    borderColor: '#FF7170', //   
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
    '&:hover': {
      backgroundColor: '#FF5E62', // hover    
      borderColor: '#FF5E62', // hover    
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#FF494D', //     
      borderColor: '#FF494D', //     
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(255,113,112,.5)',
    },
  });

const LoginAdmin = () => {
    const [userid,setUserid] = useState('');
    const [pass,setPass] = useState('');
    const [token,setToken] = useState(null);

    useEffect(()=>{
        let session_token = sessionStorage.token;
        console.log(session_token);
        setToken(session_token);
    },[]);//   

    const buttonLoginEvent = () =>{
        axios.post("/login/auth",{userid,pass})
        .then(res=>{
            if(res.data.result==='noid'){
                Swal.fire(" ");
            }else if(res.data.result==='nopass'){
                Swal.fire("  ");
            }else{
                //    token  
                sessionStorage.token=res.data.token;
                setToken(res.data.token);
            }
        })
    }

    const navigate = useNavigate();

    const goBack = () => {
        //   
        navigate(-1);
    };

    return (
      <div>
        {
          token==null?
          <div style={{paddingTop : "55px", paddingRight : "55px", paddingLeft : "55px"}}>
            <ArrowBackIcon onClick={goBack} style={{ cursor: 'pointer' }} />
            <br/><br/><br/>
            <h1 style={{color : "#FF494D", textAlign: "center" }}>TODAC</h1>
            <br/><br/>
            <h5 style={{color : "#536179"}}> </h5>
            <h6 style={{color : "#5279FD"}}>  .</h6><br/>
            <input type='text' className='form-control'
             placeholder=""
             value={userid} onChange={(e)=>setUserid(e.target.value)}/>
            <br/>
            <input type='password' className='form-control'
             placeholder=""
             value={pass} onChange={(e)=>setPass(e.target.value)}/>
             <br/>
             <CustomButton variant="contained"
              style={{width : "100%"}}
              onClick={buttonLoginEvent}></CustomButton>
          </div>
        :
          <div>
              <h4 className='alert alert-danger'> </h4>
              <br/><br/>
              <button type='button' className='btn btn-danger'
              onClick={()=>{
                  sessionStorage.removeItem("token");
                  setToken(null);
                  setUserid('');
                  setPass('');
              }}></button>
          </div>
        }
      </div>
        
    );
};

export default LoginAdmin;