import { Button } from "@mui/material";
import axios from "axios";
import React, { useState } from "react";
import { useNavigate } from "react-router";

const BoardForm = () => {
    const [photo, setPhoto] = useState("");
    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
    const [counselorcode, setCounselorCode] = useState("");

    const navi = useNavigate();

    const imageUrl = "https://kr.object.ncloudstorage.com/guest-hch/TODAC/"; //ncloud  

    //  
    const onUploadEvent = (e) => {
        const uploadFile = new FormData();
        uploadFile.append("upload", e.target.files[0]);

        //.post  axios.post()     
        axios({
            method: "post",
            url: "/form/upload",
            data: uploadFile,
            headers: { "Content-Type": "multipart/form-data" },
        }).then((res) => {
            setPhoto(res.data); //    
        });
    };

    //    
    const onCounselorRadioChange = (counselor) => {
        //       
        setCounselorCode(counselor);
    };

    // 
    const addDataEvent = () => {
        axios.post("/form/insert", { photo, title, content, counselorcode }).then((res) => {
            //    
            navi("/user/community/board");
        });
    };

    return (
        <div className='form-group mx_30'>
            <h1 style={{ textAlign: "center", marginTop: "10px" }}> </h1>
            <div className='d-flex justify-content-between'>
                <div className='col-3'>
                    <h5></h5>
                    <input type='file' className='form-control' onChange={onUploadEvent} />
                    <img alt='' src={imageUrl + photo} width={130} />
                    <b>{photo}</b>
                </div>

                <div className='col-8'>
                    <h5></h5>
                    <input
                        type='text'
                        className='form-control'
                        onChange={(e) => {
                            setTitle(e.target.value);
                        }}
                    />
                </div>
            </div>

            <div className='form-group' style={{ marginTop: "10px" }}>
                <h5></h5>
                <textarea
                    type='text'
                    className='form-control'
                    style={{ height: "250px" }}
                    onChange={(e) => {
                        setContent(e.target.value);
                    }}
                />
            </div>

            <div className='form-group' style={{ marginTop: "10px" }}>
                <h5></h5>
                <div style={{ display: "flex" }}>
                    {Array.from(Array(5).keys()).map((index) => (
                        <label key={index}>
                            <input
                                type='radio'
                                name='counselor'
                                onChange={() => onCounselorRadioChange(`${index + 1}`)}
                            />
                            {index + 1}
                        </label>
                    ))}
                </div>
            </div>
            <Button type='button' style={{ backgroundColor: "blue", color: "white" }} onClick={addDataEvent}>
                
            </Button>
        </div>
    );
};

export default BoardForm;
